.sub-vczapi-pro-menu-admin {
  float: unset;

  li:after {
    content: '|';
    padding-left: 3px;
  }

  li:last-child:after {
    content: '';
  }
}

.vczapi-pro-admin-content-wrap {
  padding: 20px 10px;

  .vczapi-pro-admin-webhook-table {
    margin: 20px 0;
    font-size: 14px;
    text-align: left;
    border-spacing: 0 15px;

    tbody {
      th {
        padding: 10px 0;
        vertical-align: middle;
        width: 216px;
      }

      .vczapi-pro-admin-webhook-endpoint-text {
        position: relative;

        span {
          position: absolute;
          top: 20px;
          left: 20px;
        }
      }

      td {
        width: 80%;

        input {
          border: 1px solid #dededede;
          background: #fff;
          padding: 14px 45px;
          width: 100%;
        }

        a {
          text-decoration: none;
        }
      }
    }
  }
}

.vczapi-pro-admin-notice-error,
.vczapi-pro-admin-notice-success {
  padding: 10px;
  color: #fff;

  p {
    margin: 0;
  }
}

.vczapi-pro-admin-notice-error {
  background: #ad1c1c;
}

.vczapi-pro-admin-notice-success {
  background: #186317;
}