.vczapi-data-table {
  position: relative;
  margin: 15px 0 !important;
  border: 1px solid #e6e6e6 !important;

  thead {
    text-align: left;

    tr {
      background: #f5f5f5;
    }
  }

  th,
  td {
    padding: 12px 15px !important;
    white-space: nowrap;
    border-bottom-color: #e6e6e6 !important;
  }

  tbody {
    .odd {
      background-color: #fff;
    }

    .even {
      background-color: rgba(0, 0, 0, .05);
    }
  }
}

.vczapi-registration-addtional-fields {
  margin: 0 !important;
  padding:0;

  tbody{
    td {
      padding: 5px 15px !important;
    }
  }
}

#vczapi-pro-registrants {
  .dt-buttons {
    float: left;
    margin: 8px 0px 0;

    .dt-button {
      padding: 10px;
    }
  }
}

.dataTables_filter,
.dataTables_length {
  margin: 15px 0;
}

